<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6" v-for="(site,itemIndex) in vueRecommend" :key="itemIndex">
        <el-card shadow="hover" class="box-card" @click="">
          <a :href="site.siteUrl" target="_blank">
            <img :src="site.siteIcon" class="card-icon">
            <div class="site-content">
              <div class="site-title" :title="site.siteTitle">
                {{site.siteTitle}}
              </div>
              <div class="site-description" :title="site.siteDescription">
                {{site.siteDescription}}
              </div>
            </div>
          </a>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'MainContent',
  data() {
    return {
      vueRecommend: [
        {
          siteIcon: require("@/assets/img/other/res/iconfont.png"),
          siteTitle: 'Iconfont',
          siteDescription: 'Iconfont-国内功能很强大且图标内容很丰富的矢量图标库，提供矢量图标下载、在线存储、格式转换等功能。阿里巴巴体验团队倾力打造，设计和前端开发的便捷工具',
          siteUrl: "https://www.iconfont.cn/"
        },
        {
          siteIcon: require("@/assets/img/other/res/uigradients.png"),
          siteTitle: 'uiGradients',
          siteDescription: '渐变色生成网站',
          siteUrl: "https://uigradients.com/"
        },
        {
          siteIcon: require("@/assets/img/other/res/aconvert.png"),
          siteTitle: 'ACONVERT.COM',
          siteDescription: '在线转换图像文件 - 将图像文件从一种格式转换为另一种格式，比如PNG到JPG，JPG到PNG，HEIC到JPG，WEBP到JPG，DDS到PNG，PNG到SVG等。',
          siteUrl: "https://www.aconvert.com/cn/image/"
        },
      ]
    }
  },
}
</script>

<style>
a {
  text-decoration: none;
  color: #373e4a;
}

.card-icon {
  height: 40px;
  width: 40px;
  float: left;
  margin-right: 10px;
}

.box-card {
  border-radius: 12px;
  height: 90px;
  margin-top: 15px;
  /* display: flex; */
}

.site-title {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
}

.site-description {
  color: #979898;
  font-size: 13px;
  margin: 2px;
  word-break: break-word;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
</style>


